import React, { useState, useEffect } from "react";
import "./Todo.css";
import { v4 } from "uuid";

export default function Todo() {
  const [inputState, setInputState] = useState("");

  const [empty, setEmpty] = useState(false);

  const [tasks, setTasks] = useState([
    { txt: "Faire du sport", id: v4() },
    { txt: "Faire le ménage", id: v4() },
    { txt: "Faire les courses", id: v4() },
    { txt: "Apprendre React", id: v4() },
  ]);

  const handleInput = (e) => {
    setInputState(e);
  };

  const toggleEmpty = () => {
    setEmpty(true);
  };

  useEffect(() => {
    setTimeout(() => {
      setEmpty(false);
    }, 1000);
    return () => {
      clearTimeout();
    };
  }, [empty]);

  const addTask = (e) => {
    e.preventDefault();

    if (inputState === "") {
      toggleEmpty();
    } else {
      const newArr = [...tasks];
      newArr.push({ txt: inputState, id: v4() });
      setTasks(newArr);
    }
    setInputState("");
  };

  console.log(empty);

  const deleteTask = (id) => {
    const filterd = tasks.filter((task) => id !== task.id);

    setTasks(filterd);
  };

  return (
    <main>
      <h1>To-do list</h1>
      <form onSubmit={addTask}>
        <label htmlFor="todo">Nouvelle tâche :</label>
        <div className="container-input">
          <input
            value={inputState}
            onChange={(e) => handleInput(e.target.value)}
            type="text"
            id="todo"
          />
          <button>AJOUTER</button>
        </div>
      </form>
      {empty && <div className="empty">Veuillez renseigner un tâche !</div>}
      <ul>
        {tasks.map((task) => (
          <li key={task.id} className="task">
            {task.txt}
            <button onClick={(id) => deleteTask(task.id)}>
              <i className="fas fa-trash-alt"></i>
            </button>
          </li>
        ))}
      </ul>
    </main>
  );
}
